import { useEffect, useState } from 'react'

import { useNavigate, useParams } from 'react-router-dom'

import { Box, CircularProgress, Alert } from '@mui/material'

import { getTrainingExecution, updateTrainingExecution } from '@/api/trainingExecutions'
import { routes } from '@/routes'
import type { TrainingExecution } from '@/types/database'

import {
  ExecutionForm,
  executionToFormData,
  formDataToExecutionUpdate,
  type ExecutionFormData,
} from '../components'

export function ExecutionsEditPage() {
  const { id } = useParams<{ id: string }>()
  const navigate = useNavigate()
  const [execution, setExecution] = useState<TrainingExecution | null>(null)
  const [loading, setLoading] = useState(true)
  const [submitting, setSubmitting] = useState(false)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    if (id) {
      loadExecution(Number(id))
    }
  }, [id])

  async function loadExecution(executionId: number) {
    try {
      setLoading(true)
      const data = await getTrainingExecution(executionId)
      setExecution(data)
    } catch (err) {
      setError('훈련 기록을 불러오는데 실패했습니다.')
      // eslint-disable-next-line no-console
      console.error(err)
    } finally {
      setLoading(false)
    }
  }

  async function handleSubmit(data: ExecutionFormData) {
    if (!execution) return

    if (!data.planId) {
      setError('훈련 계획을 선택해주세요.')
      return
    }

    try {
      setSubmitting(true)
      setError(null)
      await updateTrainingExecution(execution.id, formDataToExecutionUpdate(data))
      navigate(routes.executions.detail.$path({ id: execution.id }, {}))
    } catch (err) {
      setError('훈련 기록 수정에 실패했습니다.')
      // eslint-disable-next-line no-console
      console.error(err)
    } finally {
      setSubmitting(false)
    }
  }

  function handleCancel() {
    if (execution) {
      navigate(routes.executions.detail.$path({ id: execution.id }, {}))
    } else {
      navigate(routes.executions.$())
    }
  }

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', py: 4 }}>
        <CircularProgress />
      </Box>
    )
  }

  if (!execution) {
    return <Alert severity="warning">훈련 기록을 찾을 수 없습니다.</Alert>
  }

  return (
    <ExecutionForm
      initialData={executionToFormData(execution)}
      onSubmit={handleSubmit}
      onCancel={handleCancel}
      loading={submitting}
      error={error}
      title="훈련 기록 수정"
      submitLabel="저장"
      loadingLabel="저장 중..."
    />
  )
}
