import { useState, useEffect } from 'react'

import dayjs from 'dayjs'
import 'dayjs/locale/ko'

import {
  Box,
  Typography,
  TextField,
  Button,
  Stack,
  Alert,
  CircularProgress,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from '@mui/material'
import { DateTimePicker, LocalizationProvider } from '@mui/x-date-pickers'
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs'

import { getTrainingPlans } from '@/api/trainingPlans'
import NumberSpinner from '@/components/NumberSpinner'
import type { TrainingPlan } from '@/types/database'

export interface ExecutionFormData {
  planId: number
  unitCount: number
  durationSeconds: number
  startedAt: string
  completedAt: string
}

interface ExecutionFormProps {
  initialData: ExecutionFormData
  onSubmit: (data: ExecutionFormData) => Promise<void>
  onCancel: () => void
  loading: boolean
  error: string | null
  submitLabel: string
  loadingLabel: string
  title: string
}

export function ExecutionForm({
  initialData,
  onSubmit,
  onCancel,
  loading,
  error,
  submitLabel,
  loadingLabel,
  title,
}: ExecutionFormProps) {
  const [plans, setPlans] = useState<TrainingPlan[]>([])
  const [plansLoading, setPlansLoading] = useState(true)
  const [formData, setFormData] = useState<ExecutionFormData>(initialData)

  const selectedPlan = plans.find((p) => p.id === formData.planId)

  useEffect(() => {
    loadPlans()
  }, [])

  async function loadPlans() {
    try {
      setPlansLoading(true)
      const data = await getTrainingPlans()
      setPlans(data)
    } catch (err) {
      // eslint-disable-next-line no-console
      console.error(err)
    } finally {
      setPlansLoading(false)
    }
  }

  function formatDuration(seconds: number): string {
    const hours = Math.floor(seconds / 3600)
    const minutes = Math.floor((seconds % 3600) / 60)
    const secs = seconds % 60
    return `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
  }

  function parseDuration(value: string): number {
    const parts = value.split(':').map(Number)
    if (parts.length === 3) {
      return parts[0] * 3600 + parts[1] * 60 + parts[2]
    }
    if (parts.length === 2) {
      return parts[0] * 60 + parts[1]
    }
    return parts[0] || 0
  }

  async function handleSubmit(e: React.FormEvent<HTMLFormElement>) {
    e.preventDefault()
    await onSubmit(formData)
  }

  if (plansLoading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', py: 4 }}>
        <CircularProgress />
      </Box>
    )
  }

  return (
    <Box component="form" onSubmit={handleSubmit}>
      <Typography variant="h5" gutterBottom>
        {title}
      </Typography>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}

      <Stack spacing={3}>
        <FormControl fullWidth>
          <InputLabel>훈련 계획</InputLabel>
          <Select
            value={formData.planId}
            label="훈련 계획"
            onChange={(e) => setFormData((prev) => ({ ...prev, planId: e.target.value as number }))}
          >
            {plans.map((plan) => (
              <MenuItem key={plan.id} value={plan.id}>
                {plan.name} ({plan.unit_amount} {plan.unit_name}/회)
              </MenuItem>
            ))}
          </Select>
        </FormControl>

        <NumberSpinner
          label={`훈련량 (${selectedPlan?.unit_name ?? '단위'})`}
          min={1}
          value={formData.unitCount}
          onValueChange={(value) => {
            if (value) {
              setFormData((prev) => ({ ...prev, unitCount: value }))
            }
          }}
        />

        <TextField
          label="소요 시간 (HH:MM:SS)"
          value={formatDuration(formData.durationSeconds)}
          onChange={(e) =>
            setFormData((prev) => ({ ...prev, durationSeconds: parseDuration(e.target.value) }))
          }
          placeholder="00:00:00"
          fullWidth
          helperText="시:분:초 형식으로 입력"
        />

        <LocalizationProvider dateAdapter={AdapterDayjs} adapterLocale="ko">
          <DateTimePicker
            label="시작 시간"
            value={dayjs(formData.startedAt)}
            format="YYYY-MM-DD HH:mm"
            onChange={(value) =>
              setFormData((prev) => ({
                ...prev,
                startedAt: value?.toISOString() ?? prev.startedAt,
              }))
            }
            slotProps={{ textField: { fullWidth: true } }}
          />

          <DateTimePicker
            label="완료 시간"
            value={dayjs(formData.completedAt)}
            format="YYYY-MM-DD HH:mm"
            onChange={(value) =>
              setFormData((prev) => ({
                ...prev,
                completedAt: value?.toISOString() ?? prev.completedAt,
              }))
            }
            slotProps={{ textField: { fullWidth: true } }}
          />
        </LocalizationProvider>

        <Stack direction="row" spacing={2}>
          <Button variant="outlined" onClick={onCancel} disabled={loading}>
            취소
          </Button>
          <Button type="submit" variant="contained" disabled={loading}>
            {loading ? loadingLabel : submitLabel}
          </Button>
        </Stack>
      </Stack>
    </Box>
  )
}
