import type { TrainingExecution } from '@/types/database'

import type { ExecutionFormData } from './ExecutionForm'

export function executionToFormData(execution: TrainingExecution): ExecutionFormData {
  return {
    planId: execution.plan_id,
    unitCount: execution.unit_count,
    durationSeconds: execution.duration_seconds,
    startedAt: execution.started_at,
    completedAt: execution.completed_at,
  }
}

export function formDataToExecutionUpdate(data: ExecutionFormData) {
  return {
    plan_id: data.planId,
    unit_count: data.unitCount,
    duration_seconds: data.durationSeconds,
    started_at: data.startedAt,
    completed_at: data.completedAt,
  }
}
